import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment.prod';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class NotificationService {
  api = environment.apiUrl;  
  whatsappAPI = environment.whatsappAPI;
  headerOption = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };

  constructor(private http: HttpClient) { }

  sendSMS(body : any){
    const url = this.api + 'CommunicationHandler/SMS';    
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  sendEmail(body : any){
    const url = this.api + 'CommunicationHandler/Email';    
    return this.http.post<any>(url, body.toString(), this.headerOption).toPromise();
  }
  sendWhatsApp(body :any){
    const url = this.whatsappAPI;   
    return this.http.post<any>(url, body.toString()).toPromise();
  }
}
